import React from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import CreateRoom from "./routes/CreateRoom";
import Room from "./routes/Room";

function App() {
  console.log("Hello world");
  return (
    <BrowserRouter>
      <Switch>
      <Route path="/video_chat/room/:roomID" component={Room} />
        <Route path="/video_chat/" exact component={CreateRoom} />
      </Switch>
    </BrowserRouter>
  );
}

export default App;
