import React, { useEffect, useRef, useState } from "react";
import io from "socket.io-client";
import Peer from "peerjs"
import styled from "styled-components";




const Container = styled.div`
    padding: 20px;
    display: flex;
    height: 100vh;
    width: 90%;
    margin: 20;
    flex-wrap: wrap;
`;

const StyledVideo = styled.video`
    height: 30%;
    width: 30%;
    margin-left: 20px;
`;

const Video = (props) => {
    const ref = useRef();

    useEffect(() => {
            ref.current.srcObject = props.src;
        
    }, []);

    return (
        <StyledVideo playsInline autoPlay ref={ref} />
    );
}




const Room = (props) =>
{

    const [peers, setpeers] = useState([]);
    const peer = new Peer(undefined,{
        host:'safe-cliffs-70260.herokuapp.com',
        path:'/peerjs',
        secure:true
    })

    peer.on('error',(err)=>
        {
            console.log(err);
        })
       
        peer.on('open',user_id=>
        {
            console.log(user_id);
            socket.emit("userjoin",user_id);
        })
const socket = io.connect('wss://safe-cliffs-70260.herokuapp.com/');


    useEffect(()=>
    {
          
        navigator.mediaDevices.getUserMedia({
            video:true,
            audio:true
        }).then(stream=>
        {
         
            addingVideo(stream);
            socket.on("userjoined",user_id=>
            {
                console.log("UserID is ",user_id)
               
                connectOther(user_id,stream);
            })   

            peer.on('call',call =>
            {
                console.log("Calling");
                call.answer(stream);
                call.on('stream',user_video=>
                {
                    addingVideo(user_video);
                })
            })
        
    })
        
    const connectOther= (user_id,stream) =>
    {
       
        const call = peer.call(user_id,stream);
        call.on('stream',user_video=>
        {
            addingVideo(user_video)
        })
    }

const addingVideo = (user_video) =>
{
     setpeers(peers =>[...peers,user_video])   
}       
    },[])
       
return(
    <Container>

    {peers.map((video_data,index)=>
    {
return(
    <Video key={index} src={video_data}></Video>)
    })}

    </Container>
)
}

export default Room;
